.message {

    padding-right: 70px;
    margin-top:24px;


}


.imageAndText {
    display: flex;
    flex-direction:row-reverse;
}

.imageAndText img {
    margin-top: 12px;
    width: 48px;
    height: 48px;
}

.text {

    background: #0066CC;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    width: 527px;
    height: 60px;
    border-radius: 16px;
    position: relative;
    margin-right: 20px;
    margin-bottom:5px;

}

.text:after {
    content: "";
    width: 39px;
    height: 27px;
    border-left: 3px ;
    border-bottom: 5px ;
    position: absolute;
    bottom: 0;
    right: -27px;
    transform: scale(-1,1);

    background: radial-gradient(circle at top left, transparent 58%, #0066CC 10%);
}

.name {
    display: flex;
    justify-content: flex-end;
    margin: 7px 25px 3px 0px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
}

.messageText {
    margin: 3px 25px 8px 13px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
}

.time {
    display: flex;
    justify-content: flex-end;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    padding-right: 11px;

    color: #000000;
}